.field {
  position: relative;
  border-bottom: 2px dotted lightgray;
  margin-top: 2.5rem;
  margin-left: 0.3rem;
  margin-right: 0.5rem;
  margin-bottom: 1rem;
}

.input {
  outline: none;
  border: none;
  overflow: hidden;
  margin: 0;
  width: 100%;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  background-color: #f0ecec;
  padding: 0.25rem 0;
  color: black;
  font-size: 1.2rem;
  font-weight: bold;
}
.input:valid {
  color: #707070;
}
.input:invalid {
  color: #cc0000;
}
.field::after {
  content: "";
  position: relative;
  display: block;
  height: 4px;
  width: 100%;
  background: black;
  transform: scaleX(0);
  transform-origin: 0%;
  transition: transform 500ms ease;
  top: 1px;
}
.field:focus-within {
  border-color: transparent;
}
.field:focus-within::after {
  transform: scaleX(1);
}

